import { Facebook, Instagram, Twitter } from '@material-ui/icons'
import React from 'react'
import styled from 'styled-components'


const Container = styled.div`
    display: flex;
    align-items: center;
    background-color: #583101;
    color: #FFEDD8;
`

const Left = styled.div`
    flex: 2;
`

const Title = styled.h3`
    margin-bottom: 24px;
    font-weight: 500;
    font-family: "Playfair Display", serif;
    font-style: italic;
    font-size: 24px;
`

const Icons = styled.div`
    margin-bottom: 24px;
    
`

const Icon = styled.i`
    font-size: 12px;
    margin-right: 6px;
`

const Email = styled.p`

`

const Right = styled.div`
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
`

const L = styled.div`

`

const R = styled.div`
    margin-left: 50px;
`

const TitleF = styled.h5`
    margin-bottom: 12px;
`

const Lists = styled.ul`
    
`

const List = styled.ol`
    margin-bottom: 5px;
    cursor: pointer;
`

const Footer = () => {
  return (
    <Container className='u-pad-1'>
        <Left>
            <Title>
                Eleventh House
            </Title>
            <Icons>
                <Icon>
                    <Facebook/>
                </Icon>
                <Icon>
                    <Instagram/>
                </Icon>
                <Icon>
                    <Twitter/>
                </Icon>
            </Icons>
            <Email>
                ayoadebiyi95@gmail.com
            </Email>
        </Left>
        <Right>
            <L>
                <TitleF>
                    LEARN MORE
                </TitleF>
                <Lists>
                    <List>
                        Success stories
                    </List>
                    <List>
                        Why us?
                    </List>
                    <List>
                        How it works
                    </List>
                    <List>
                        Social media
                    </List>
                    <List>
                        FAQs
                    </List>
                </Lists>
            </L>
            <R>
                <TitleF>
                    RESOURCES
                </TitleF>
                <Lists>
                    <List>
                        Help center
                    </List>
                    <List>
                        Blog
                    </List>
                    <List>
                        Career
                    </List>
                    <List>
                        Press center
                    </List>
                    <List>
                        About us 
                    </List>
                </Lists>
            </R>
        </Right>
    </Container>
  )
}

export default Footer
