import React from 'react'
import styled from 'styled-components'
import { useTheme } from '../Context/ThemeContext'

const Bl = styled.span`
    position: absolute;
    top: -5px;
    right: -55%;
    width: 120px;
    height: 120px;
    background: rgba(255,255,255,0.2);
    border-radius: 50%;
    transition: all .4s cubic-bezier(.21,.69, .73, 1.08);
`

const Container = styled.a`
    cursor: pointer;
    position: relative;
    overflow: hidden;
    padding: .8rem 1rem;
    border-radius: 10px;
    background: ${props => props.Theme.colorSecondary};
    color: ${props => props.Theme.colorNeutral} !important;
    font-size: 0.8rem;
    font-family: ${props => props.Theme.fontSecondary};
    transition: all 0.4s ease-in-out;
    display: inline-block;
    &:hover ${Bl} {
        right: -30%;
        top: -15px;
    }
`

const Ar = styled.span`
    margin-left: .5rem;
`

const Ic = styled.i`
    font-size: 0.8rem;
    animation: moveLR 2s infinite;
    @keyframes moveLR {
        0%{
            transform: translateX(0);
        }
        50%{
            transform: translateX(7px);
        }
        100%{
            transform: translateX(0);
        }
    }
`

const Button = ({Name, Icon, Arrow, Blob}) => {
    const Theme = useTheme()
  return (
    <Container Theme={Theme}>
        {Name}
        <Ar className={Arrow}>
            <Ic className={Icon}></Ic>
        </Ar>
        <Bl className={Blob}></Bl>
    </Container>
  )
}

export default Button