import { createGlobalStyle } from "styled-components";

const GlobalStyle = createGlobalStyle`
    *{
        margin: 0;
        padding: 0;
        text-decoration: none;
        list-style: none;
        box-sizing: border-box;
    }

    body{
        position: relative;
        font-size: 1.2rem; 
    }

    //Utility Classes
    .u-pad-1{
        padding: 3rem 3rem;
    }

    .u-pad-2{
        padding: 1rem 18rem 6rem 18rem;
    }
`

export default GlobalStyle