import React from 'react'
import styled from 'styled-components'

const Container = styled.div`
    color: #583101;
    
`

const Con = styled.div`
    display: flex;
    align-items: center;
    margin-bottom: 16px;
`

const ConN = styled.h4`
    font-size: 15px;
    font-weight: 500;
    line-height: 16px;
`

const ConT = styled.h2`
    font-size: 30px;
    font-weight: 500;
    margin-bottom: 16px;
`

const Image = styled.img`
    margin-left: 10px;
    width: 60px;
    height: 12px;
`

const MinTitle = ({Name, Img, Title}) => {
  return (
    <Container>
        <Con>
            <ConN>
                {Name}
            </ConN>
            <Image src={Img}/>
        </Con>
        <ConT>
            {Title}
        </ConT>
    </Container>
  )
}

export default MinTitle