import React from 'react'
import styled from 'styled-components'
import HeaderContent from './HeaderContent'
import NavBar from './NavBar'

const Container = styled.div`
    min-height: 100vh;
    position: relative;
`

const Header = () => {
  return (
    <Container>
        <NavBar/>
        <HeaderContent/>
    </Container>
  )
}

export default Header