import React from 'react'
import styled from 'styled-components'
import MinTitle from './MinTitle'

const Container = styled.div`

`

const Top = styled.div`

`

const MainPara = styled.div`
    text-align: justify;
`

const Para = styled.p`
    font-family: 'Poppins', sans-serif;
    font-size: 13px;
`

const TitleCon = styled.div`
    display: grid;
    grid-template-columns: 2fr 1fr;
    margin-bottom: 16px;
`

const Bottom = styled.div`
    display: grid;
    grid-template-columns: repeat(3, 1fr) !important;
    grid-template-rows: repeat(3, 200px) !important;
    grid-gap: .5rem;
`

const Con1 = styled.div`
    grid-row: 1/3;
    position: relative;
    cursor: pointer;
    &:hover{
        transform: scale(1.02);
    }
`

const Con2 = styled.div`
    grid-area: 1/2/3/4;
    position: relative;
    cursor: pointer;
    &:hover{
        transform: scale(1.01);
    }
`

const Con3 = styled.div`
    position: relative;
    cursor: pointer;
    &:hover{
        transform: scale(1.02);
    }
`

const Con4 = styled.div`
    position: relative;
    cursor: pointer;
    &:hover{
        transform: scale(1.02);
    }
`

const Con5 = styled.div`
    position: relative;
    cursor: pointer;
    &:hover{
        transform: scale(1.02);
    }
`

const Image = styled.img`
    width: 100%;
    height: 100%;
    object-fit: cover;
`

const Price = styled.div`
    position: absolute;
    left: 0;
    top: 0;
    background: white;
    padding: .5rem;
    display: flex;
    align-items: center;
    justify-content: center;
`
const Charge = styled.h6`

`

const Desc = styled.h5`
    position: absolute;
    left: 3%;
    bottom: 5%;
    color: white;
`

const OurChoice = () => {
  return (
    <Container className='u-pad-1'>
        <Top>
            <TitleCon>
                <MinTitle
                    Name={'OUR CHOICE'}
                    Img={'https://i.imgur.com/Ua9QEta.png'}
                    Title={'The best room just for you!'}
                />
                <MainPara>
                    <Para>
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit. 
                        Enim, risus dictum sed sit. Suspendisse congue est pellentesque nec. 
                        Sed tincidunt aliquam morbi diam faucibus sodales.
                    </Para>
                </MainPara> 
            </TitleCon>
        </Top>
        <Bottom>
            <Con1>
                <Image src='https://i.imgur.com/lzZKPyk.png'/>
                <Price>
                    <Charge>
                        $300/night
                    </Charge>
                </Price>
                <Desc>
                    Couple Single Room
                </Desc>
            </Con1>
            <Con2>
                <Image src='https://i.imgur.com/lBW5tMM.png'/>
                <Price>
                    <Charge>
                        $300/night
                    </Charge>
                </Price>
                <Desc>
                    Luxe Room
                </Desc>
            </Con2>
            <Con3>
                <Image src='https://i.imgur.com/18Ymaej.png'/>
                <Price>
                    <Charge>
                        $300/night
                    </Charge>
                </Price>
                <Desc>
                    Premium Room
                </Desc>
            </Con3>
            <Con4>
                <Image src='https://i.imgur.com/G9bTYp1.png'/>
                <Price>
                    <Charge>
                        $300/night
                    </Charge>
                </Price>
                <Desc>
                    Master Room
                </Desc>
            </Con4>
            <Con5>
                <Image src='https://i.imgur.com/L3ykAlz.png'/>
                <Price>
                    <Charge>
                        $300/night
                    </Charge>
                </Price>
                <Desc>
                    Family Luxe
                </Desc>
            </Con5>
        </Bottom>
    </Container>
  )
}

export default OurChoice