import { Add } from '@material-ui/icons'
import React from 'react'
import styled from 'styled-components'
import Button from './Button'
import MinTitle from './MinTitle'

const Container = styled.div`
    
`

const TitleContainer = styled.div`
    margin-bottom: 24px;
    width: 934px;
`

const Para = styled.div`
    padding-right: 415px;
    text-align: justify;
    font-family: 'Poppins', sans-serif;
    font-size: 13px;
    font-weight: 400;
`

const MainContainer = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
`

const Wrapper = styled.div`
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
`

const LoremContainer = styled.div`
    
`

const Lorem = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 1rem;
    margin-bottom: 1.5rem;
    border-bottom: 1px solid #583101;
`

const Text = styled.div`

`

const Btn4 = styled.div`
    display: flex;
    align-items: center;
    margin-top: 32px;
`

const More = styled.p`
    margin-right: 64px;
`

const Faq = () => {
  return (
    <Container className='u-pad-1'>
      <TitleContainer>
        <MinTitle
            Name={'FAQs'}
            Img={'https://i.imgur.com/Ua9QEta.png'}
            Title={'Frequently Asked Questions'}
        />
        <Para>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. 
            Enim, risus dictum sed sit. Suspendisse congue est pellentesque nec.
        </Para>
      </TitleContainer>
      <MainContainer>
        <Wrapper>
            <LoremContainer>
                <Lorem>
                    <Text>
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit. Enim, risus dictum sed
                    </Text> 
                    <Add style={{fontSize: 14, fontWeight: 500, marginLeft: 100}}/>
                </Lorem>
                <Lorem>
                    <Text>
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit. Enim?
                    </Text>
                    <Add style={{fontSize: 14, fontWeight: 500, marginLeft: 100}}/>
                </Lorem>
                <Lorem>
                    <Text>
                        ipsum dolor sit amet, consectetur adipiscing elit. Enim?
                    </Text>
                    <Add style={{fontSize: 14, fontWeight: 500, marginLeft: 100}}/>  
                </Lorem>
            </LoremContainer>
            
            <Btn4>
                <More>
                    Have more questions?
                </More>
                <Button
                    Name={'Book Now'}
                    Icon={'fas fa-chevron-right'}
                    Arrow={'Arrow'}
                    Blob={'Blob'}
                />
            </Btn4>
        </Wrapper>
        
      </MainContainer>
    </Container>
  )
}

export default Faq
