import React from 'react'
import styled from 'styled-components'
import Button from './Button'
import MinTitle from './MinTitle'

const Container = styled.div`
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 380px;
    grid-gap: 3rem;
`

const Left = styled.div`
    height: 100%;
`

const Image = styled.img`
    width: 100%;
    height: 100% !important;
    object-fit: cover;
`

const Right = styled.div`

`

const Desc = styled.p`
    font-family: 'Poppins', sans-serif;
    font-size: 13px;
    text-align: justify;
    margin-bottom: 16px;
    color: black;
`

const Discount = styled.div`
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    color: black;
    margin-bottom: 50px;
`

const LeftD = styled.div`
    padding: 7px 24px 7px 0;
`

const RightD = styled.div`
    padding: 7px 24px 7px 24px;
    border-left: 1px solid #583101;
`

const Amount = styled.h3`
    font-size: 15px;
    letter-spacing: 1px;
    margin-bottom: 16px;
`

const Number = styled.span`
    font-size: 32px;
`

const Duration = styled.h5`
    font-size: 17px;
    letter-spacing: 2px;
`

const Featured = () => {
  return (
    <Container className='u-pad-1'>
        <Left>
            <Image src='https://i.imgur.com/RmCNzvu.png'/>
        </Left>
        <Right>
            <MinTitle
                Name={'FEATURED OFFER'}
                Img={'https://i.imgur.com/Ua9QEta.png'}
                Title={'Economy Luxe Room'}
            />
            <Desc>
                Lorem ipsum dolor sit amet, consectetur adipiscing elit. 
                Enim, risus dictum sed sit. Suspendisse congue est pellentesque nec. 
                Sed tincidunt aliquam morbi diam faucibus sodales. Auctor lacus, 
                sed amet velit leo, felis eget in proin. Arcu enim, 
                est dictum venenatis convallis tortor. Malesuada vestibulum a sed libero sapien, tellus.
            </Desc>
            <Discount>
                <LeftD>
                    <Amount>
                        <Number>25%</Number> DISCOUNT
                    </Amount>
                    <Duration>
                        STAY 6 MONTHS OR MORE
                    </Duration>
                </LeftD>
                <RightD>
                    <Amount>
                        <Number>10%</Number> DISCOUNT
                    </Amount>
                    <Duration>
                        STAY 2 MONTHS OR MORE
                    </Duration>
                </RightD>
            </Discount>
            <Button
                Name={'Book Now'}
                Icon={'fas fa-chevron-right'}
                Arrow={'Arrow'}
                Blob={'Blob'}
            />
        </Right>
    </Container>
  )
}

export default Featured