import React from 'react'
import styled from 'styled-components'
import Button from './Button'
import MinTitle from './MinTitle'

const Container = styled.div`
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 2rem;
`

const Left = styled.div`
    animation: contentHide all ease-in-out;
    @keyframes contentHide {
        0%{
            opacity: 0;
            transform: translateX(-120%) scaleX(0);
        }
        100%{
            opacity: 1;
            transform: translateX(0) scaleX(1);
        }
    }
    padding: 80px 70px 80px 0;
`

const Para = styled.p`
    font-family: 'Poppins', sans-serif;
    font-size: 15px;
    font-weight: 400;
    text-align: justify;
    margin-bottom: 54px;   
`

const Right = styled.div`
    position: relative;
    transition: all .4s ease-in-out;
    animation: contentHide2;
    animation-delay: .5s;
    opacity: 0;
    animation-fill-mode: forwards;
    @keyframes contentHide2 {
        0%{
            opacity: 0;
            transform: translateY(-120%) scale(0);
            visibility: none;
        }
        100%{
            opacity: 1;
            transform: translateY(0) scale(1);
            visibility: visible;
        }
    }
`
const Image1 = styled.img`
    position: absolute;
    width: 300px;
    height: 300px;
    object-fit: cover;
    bottom: 0;
    left: 0;
    transition: all .4s ease-in-out;
`

const Image2 = styled.img`
    position: absolute;
    width: 400px;
    height: 430px;
    object-fit: cover;
    right: 0;
    top: 0;
    border-top-right-radius: 5px;
`

const AboutUs = () => {
  return (
    <Container className='u-pad-1'>
        <Left>
            <MinTitle
                Name={'ABOUT US'}
                Img={'https://i.imgur.com/Ua9QEta.png'}
                Title={'At Eleventh House Hotels'}
            />
            <Para>
                Lorem ipsum dolor sit amet, consectetur adipiscing elit. 
                Enim, risus dictum sed sit. Suspendisse congue est 
                pellentesque nec. Sed tincidunt aliquam morbi diam 
                faucibus sodales. Auctor lacus, sed amet velit leo,
                felis eget in proin. Arcu enim, est dictum venenatis 
                convallis tortor. Malesuada vestibulum a sed libero 
                sapien, tellus.
            </Para>
            <Button
                    Name={'Read More'}
                    Icon={'fas fa-chevron-right'}
                    Arrow={'Arrow'}
                    Blob={'Blob'}
                />
        </Left>
        <Right>
            <Image2 src='https://i.imgur.com/ZIv6Jal.png'/>
            <Image1 src='https://i.imgur.com/MFow6l5.png'/>
        </Right>
    </Container>
  )
}

export default AboutUs