import React from 'react'
import styled from 'styled-components'
import { useTheme } from '../Context/ThemeContext'
import Button from './Button'

const Container = styled.div`
    
`

const Navigation = styled.nav`
    display: grid;
    grid-template-columns: 200px 1fr auto;
    padding: 1rem 2rem;
`

const Logo = styled.div`
    display: flex;
    align-items: center;
`

const Lg = styled.h2`
    font-size: 1.3rem;
    font-style: italic;
    letter-spacing: .5px;
    font-family: ${props => props.Theme.fontPrimary};
`

const NavItems = styled.ul`
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-right: 4rem;
`

const NavItem = styled.li`
    margin: 0 .7rem;
`

const Link = styled.a`
    text-transform: uppercase;
    font-size: 1rem;
    padding: .5rem;
    position: relative;
    font-weight: 500;
    font-family: ${props => props.Theme.fontSecondary};
    font-size: 0.8rem;
    &::before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        transform: scale(0);
        background: ${props => props.Theme.linearGradient1};
        z-index: -1;
        border-radius: 10px;
        transition: all .4s ease-in-out;
    }
    &:hover{
        &::before{
            transform: scale(1);
        }
    }
`

const Btn1 = styled.div`

`

const NavBar = () => {
    const Theme = useTheme()
  return (
    <Container Theme={Theme}>
        <Navigation>
            <Logo>
                <Lg Theme={Theme}>
                    ELEVENTH HOUSE
                </Lg>
            </Logo>
            <NavItems >
                <NavItem>
                    <Link href='#' className='activeMenu' Theme={Theme}>
                        Home
                    </Link>
                </NavItem>
                <NavItem>
                    <Link href='#' Theme={Theme}>
                        About Us
                    </Link>
                </NavItem>
                <NavItem>
                    <Link href='#' Theme={Theme}>
                        Our Services
                    </Link>
                </NavItem>
                <NavItem>
                    <Link href='#' Theme={Theme}>
                        Blog
                    </Link>
                </NavItem>
                <NavItem>
                    <Link href='#' Theme={Theme}>
                        Contact Us
                    </Link>
                </NavItem>
            </NavItems>
            <Btn1>
                <Button
                    Name={'Book Now'}
                    Icon={'fas fa-chevron-right'}
                    Arrow={'Arrow'}
                    Blob={'Blob'}
                />
            </Btn1>
        </Navigation>
    </Container>
  )
}

export default NavBar