import { ExpandMore } from '@material-ui/icons'
import React from 'react'
import styled from 'styled-components'
import { useTheme } from '../Context/ThemeContext'

const Container = styled.div`
    height: 80vh;
    width: 100%;
    position: relative;
`

const ImageContainer = styled.div`
    height: 100%;
`

const Img = styled.img`
    object-fit: cover;
    width: 100%;
    height: 100%;
`

const TextContainer = styled.div`
    height: 50vh;
    width: 50%;
    position: absolute;
    left: 50%;
    margin-left: -25%;
    top: 50%;
    margin-top: -25vh;
    color: #FFEDD8;
    text-align: center;
`

const Title = styled.h3`
    font-size: 20px;
    font-weight: 500;
    margin-top: 20px;
    margin-bottom: 50px;    
`

const Desc = styled.h1`
    margin-bottom: 50px;
`

const CheckContainer = styled.div`
    background-color: #583101;
    height: 100px;
    width: 70%;
    position: absolute;
    left: 50%;
    margin-left: -35%;
    bottom: 2%;
    margin-bottom: -50px;
    color: #FFEDD8;
`

const CheckWrapper = styled.div`
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 2rem;
    padding: 1rem 2rem;
`

const Check = styled.div`
    display: flex;
    justify-content: center;
    flex-direction: column;

`

const CheckTitle = styled.h6`
    font-family: 'Poppins', sans-serif;
    font-size: 10px;
    font-weight: 400;
    margin-bottom: 20px;
`

const CheckDate = styled.div`
    display: flex;
    align-items: center;
    font-size: 20px;
    font-family: 'Poppins', sans-serif;
    cursor: pointer;
`

const Btn = styled.div`
    font-size: 13px;
    font-weight: 500;
    cursor: pointer;
    font-family: 'Poppins', sans-serif;
    display: flex;
    align-items: center;
`

const Bl = styled.span`
    position: absolute;
    top: -5px;
    right: -55%;
    width: 120px;
    height: 120px;
    background: rgba(255,255,255,0.2);
    border-radius: 50%;
    transition: all .4s cubic-bezier(.21,.69, .73, 1.08);
`

const Btn1 = styled.a`
    cursor: pointer;
    position: relative;
    overflow: hidden;
    padding: .8rem 1rem;
    border-radius: 10px;
    background: transparent;
    color: ${props => props.Theme.colorPrimary} !important;
    border: 1px solid ${props => props.Theme.colorPrimary};
    font-size: 0.8rem;
    font-family: ${props => props.Theme.fontSecondary};
    transition: all 0.4s ease-in-out;
    display: inline-block;
    &:hover ${Bl} {
        right: -30%;
        top: -15px;
    }
`

const Ar = styled.span`
    margin-left: .5rem;
`

const Ic = styled.i`
    font-size: 0.8rem;
    animation: moveLR 2s infinite;
    @keyframes moveLR {
        0%{
            transform: translateX(0);
        }
        50%{
            transform: translateX(7px);
        }
        100%{
            transform: translateX(0);
        }
    }
`

const HeaderContent = () => {
    const Theme = useTheme()
  return (
    <Container>
        <ImageContainer>
            <Img src='https://i.imgur.com/PtDDrSi.png'/>
        </ImageContainer>
        <TextContainer>
            <Title>
                GET LUXURY AND COMFORT
            </Title>
            <Desc>
                Welcome To The Place Luxury Meets Affordability
            </Desc>
            <Btn1 Theme={Theme}>
                Explore More
                <Ar>
                    <Ic className='fas fa-chevron-right'></Ic>
                </Ar>
                <Bl></Bl>
            </Btn1>
        </TextContainer>
        <CheckContainer>
            <CheckWrapper>
                <Check>
                    <CheckTitle>
                        Check in
                    </CheckTitle>
                    <CheckDate>
                        12 June
                        <ExpandMore style={{marginLeft: 40}}/>
                    </CheckDate>
                </Check>
                <Check>
                    <CheckTitle>
                        Check out
                    </CheckTitle>
                    <CheckDate>
                        22 June
                        <ExpandMore style={{marginLeft: 40}}/>
                    </CheckDate>
                </Check>
                <Check>
                    <CheckTitle>
                        Guest
                    </CheckTitle>
                    <CheckDate>
                        03
                        <ExpandMore style={{marginLeft: 40}}/>
                    </CheckDate>
                </Check>
                <Btn>
                    <Btn1 Theme={Theme}>
                        Explore More
                        <Ar>
                            <Ic className='fas fa-chevron-right'></Ic>
                        </Ar>
                        <Bl></Bl>
                    </Btn1>
                </Btn>
            </CheckWrapper>
        </CheckContainer>
    </Container>
  )
}

export default HeaderContent